* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
}

html {
  font-family: Circular, spotify-circular, Helvetica, Arial, sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.white {
  color: white;
}

.black {
  color: black;
}

.container {
  background-color: black;
  width: 100%;
}

.fixed {
  position: fixed;
  z-index: 1000;
}
